class Helper {
  // Simple function to convert .23 to 0.23
  static addZero = (a) => {
    if (a == null) {
      return a
    } else {
      const str = a
     
    if(!str || str=="" || str == null)
    {
      return
    }
      if (str?.charAt(0) == '.' ) {
        return '0' + str
      }else {
        return a
      }
    }
  }
  static capitalizeFirstLetter(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
  }
  
}


export default Helper


