import React, {useEffect, useState} from 'react'
import {Table, message} from 'antd'
import {mainConfig} from '../../../config/mainConfig'
import Helper from '../../utils/Helper'
import {Tooltip, Button, DatePicker, Modal, Form, Input, Pagination, Popconfirm} from 'antd'
import {MinusOutlined} from '@ant-design/icons'
import moment from 'moment'
import PapaParse from 'papaparse'
import {ApifordataPost, Apifordataget} from './ApiForgetdata'
import {Link} from 'react-router-dom'
import {Tabs} from 'react-bootstrap-v5'

let day = new Date()
let dateTodayes = moment(day).format('YYYY-MM-DD')
let dateTodayes1 = moment(day).format('DD-MMM-YYYY')

const tabs = ['', 'Buyerintel', '', '', 'BuyerWatchlist', 'Offers', '']
const {RangePicker} = DatePicker
const BuyersWatchlist = () => {
  const [data, setData] = useState<any>([])
  const [isModalOpen, setIsModalOpen] = useState<any>(false)
  const [buttonLoader, setbuttonLoader] = useState<any>(false)
  const [downloadReport, setdownloadReport] = useState<any>([])
  const [currentPage, setCurrentPage] = useState<any>(1)
  const [loading, setloading] = useState<boolean>(false)
  const [activeMenu, setActivemenu] = useState<any>('Offers')
  const [sorter, setsorter] = useState<any>({
    columnKey: 'AsOfDate',
    order: 'descend',
  })
  const [rowCnt, setrowCnt] = useState<any>(1)

  useEffect(() => {
    getBuyerWatchlist()
  }, [currentPage, sorter, activeMenu])
  let token = ''
  const getBuyerWatchlist = async () => {
    setloading(true)
    let api = `/api/get-watchlist/${tabs.indexOf(activeMenu)}/${currentPage}/${sorter.columnKey}/${
      sorter.order == 'ascend' ? 1 : 0
    }`

    let buyerwatchList: any = await Apifordataget(api)
    if (
      buyerwatchList &&
      buyerwatchList.data &&
      buyerwatchList.data.products &&
      buyerwatchList.data.products.length > 0
    ) {
      setData(buyerwatchList?.data?.products)
      setrowCnt(buyerwatchList?.data?.rowCnt)
    }

    if (buyerwatchList?.data?.error == 'No any watchlist data found') {
      setData([])
      setrowCnt(0)
    }
    setloading(false)
  }
  const columns = [
    {
      title: 'Agg Code',
      dataIndex: 'ProductAC4',
      key: 'ProductAC4',
      sorter: true,

      width: 100,
      // render: (item: any, record: any) => {

      //   return (
      //     <Tooltip placement='topLeft' title={'View Product Page'}>
      //       <Link
      //         // target='_blank'
      //         to={{
      //           pathname: '/product',

      //           state: {prod_id: record.productpage_id, data: record},
      //         }}
      //       >
      //         {item}
      //       </Link>
      //     </Tooltip>
      //   )
      // },
    },
    {
      title: 'Spot Code',
      dataIndex: 'SPOTCode',
      key: 'SPOTCode',
      sorter: true,

      width: 100,
      render: (item: any, record: any) => {
        return (
          <Tooltip placement='topLeft' title={'View Historical Pricing'}>
            <Link
              to={{
                pathname: '/productanalytics',
                state: {prod_id: record.ProductAC4, spotid: record.SPOTCode, spotCode: item},
              }}
            >
              {item}
            </Link>
          </Tooltip>
        )
      },
    },
    {
      title: 'Description',
      dataIndex: 'ProductDesc',
      key: 'ProductDesc',
      sorter: true,

      width: 200,
    },

    {
      title: 'Pack Size',
      dataIndex: 'PackSize',
      key: 'PackSize',
      width: 100,
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return Helper.addZero(item)
      },
      sorter: true,
    },
    {
      title: 'Date',
      dataIndex: 'AsOfDate',
      key: 'AsOfDate',
      width: 100,
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return moment(item).format('DD-MMM-YYYY')
      },
      sorter: true,
    },
    {
      title: 'Supplier Comment',
      dataIndex: 'supplier_comments',
      key: 'supplier_comments',
      sorter: true,

      width: 200,
    },
    {
      title: 'Buyer Comment',
      dataIndex: 'buyer_comments',
      key: 'buyer_comments',
      sorter: true,

      width: 200,
    },

    {
      title: 'Action',
      dataIndex: 'ProductAC4',
      key: 'ProductAC4',

      fixed: 'right' as 'right',
      width: 100,

      render: (item: any, record: any) => {
        if (record.AsOfDate < moment(dateTodayes).format('YYYY-MM-DD')) {
          return <></>
        }
        return (
          <Tooltip placement='topLeft' title={'Remove from watchlist'}>
            <Button icon={<MinusOutlined />} onClick={() => removeElement(record)}></Button>
          </Tooltip>
        )
      },
    },
  ]
  const removeElement = (data: any) => {
    fetch(`${mainConfig.MAIN_ENDPOINT}/api/remove-from-watchlist/${data.watchlist_id}`, {
      method: 'put',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
      .then(function (response) {
        if (response.status != 200) {
        }
        response.json().then(function (data) {
          if (data.success == true) {
            message.success(data.data)
            getBuyerWatchlist()
          }
        })
      })
      .catch(function (err) {
        console.log('Fetch Error :-S', err)
      })
  }
  const showModal = () => {
    setIsModalOpen(true)
  }

  const handleCancel = () => {
    setIsModalOpen(false)
  }
  const onFinish = (fieldsValue: any) => {
    let date1 = '',
      date2 = ''
    if (!fieldsValue.productcode && !fieldsValue.daterange) return
    if (fieldsValue.daterange) {
      date1 = moment(fieldsValue.daterange[0]).format('YYYY-MM-DD')
      date2 = moment(fieldsValue.daterange[1]).format('YYYY-MM-DD')
    }

    var payLoad = {
      prodcode: fieldsValue.productcode ? fieldsValue.productcode : '',
      sdate: date1 ? date1 : '',
      edate: date2 ? date2 : '',
    }
    fetch(
      `${mainConfig.MAIN_ENDPOINT}/api/search-watchlist/${tabs.indexOf(activeMenu)}/1/AsofDate/0`,
      {
        method: 'post',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payLoad),
      }
    )
      .then(function (response) {
        if (response.status != 200) {
          setData([])
        }
        response.json().then(function (data) {
          if (data.success == true) {
            setData(data.data.products)
          }
        })
      })
      .catch(function (err) {
        console.log('Fetch Error :-S', err)
      })
    setIsModalOpen(false)
  }
  const csvDownload = (data: any) => {
    var csv = PapaParse.unparse(data, {
      header: false,
      columns: [
        'Ranking',
        'ProductAC4',
        'SPOTCode',
        'ProductDesc',
        'PackSize',
        'Cost',
        'True Cost',
        'Avg Cost',
        'Avg Vol',
        'comments',
        'tags',
        'c87',
        'c122',
        'DC',
        'DG',
        'RH',
        'RBS',
        'ATOZ',
        'RRP',
        'Phoenix',
        'Trident',
        'AAH',
        'Colorama',
        'Bestway',
        'AsOfDate',
      ], //or array of strings
    })
    var headersVal = PapaParse.unparse({
      fields: [
        'RANKING',
        'AGG CODE',
        'SPOT CODE',
        'DESCRIPTION',
        'PACK',
        'COST',
        'TRUE COST',
        'AVG COST',
        'AVG VOL',
        'SUPPLIERS COMMENTS',
        'TAGS',
        '87',
        '122',
        'DC',
        'DG',
        'RH',
        'RBS',
        'ATOZ',
        'RRP',
        'PHOENIX',
        'TRIDENT',
        'AAH',
        'COLORAMA',
        'BESTWAY',
        'DATE',
      ],
      data: [],
    })
    var result = headersVal + csv
    var csvData = new Blob([result], {type: 'text/csv;charset=utf-8;'})
    var csvURL = null
    csvURL = window.URL.createObjectURL(csvData)
    var tempLink = document.createElement('a')
    tempLink.href = csvURL
    tempLink.setAttribute('download', `${new Date().toLocaleDateString()}.csv`)
    tempLink.click()
    setbuttonLoader(false)
  }
  const handleNotification = async () => {
    setbuttonLoader(true)
    let obj = {
      list_type: tabs.indexOf(activeMenu),
      type: '1',
      dataCount:data.lenght
    }
    await fetch(`${mainConfig.MAIN_ENDPOINT}/api/notify-user`, {
      method: 'post',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(obj),
    })
      .then(function (data) {
        data.json().then(function (data) {
          if (data.success == true) {
            message.success(data.message)
            getBuyerWatchlist()
          }
        })
      })
      .catch(function (error) {})
    setbuttonLoader(false)
  }

  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')

  return (
    <>
      <div className='pt-4 m-2'>
        <div className='card'>
          <div className='card-body py-2 '>
            <div className='row m-4 p-2'>
              <div className='col-4'>
                <div className='card-toolbar' style={{width: '100%'}}>
                  <h3 className='card-title align-items-start flex-column'>
                    <span
                      className='card-label fw-bolder fs-4 mb-1'
                      style={{color: 'rgb(138, 38, 80)'}}
                    >
                      Buyer Defined Watch List <>({dateTodayes1})</>
                    </span>
                  </h3>
                </div>
              </div>

              <div className='col-4'>
                <div className='text-center'>
                  <Tooltip placement='topLeft' title={'Notify Reviewer and Pricer'}>
                    <Popconfirm
                      title='Do you want to mark watch list as processed?'
                      onConfirm={handleNotification}
                      okText='Yes'
                      cancelText='No'
                    >
                      <Button type='primary' size={'large'} loading={buttonLoader}>
                        Notify Reviewer and Pricer
                      </Button>
                    </Popconfirm>
                  </Tooltip>
                </div>
              </div>
              <div className='col-4'>
                <div id='outer'>
                  <div className='inner p-1'>
                    {' '}
                    <Tooltip placement='topLeft' title={'Search'}>
                      <Button
                        type='primary'
                        size={'large'}
                        onClick={() => {
                          setIsModalOpen(true)
                        }}
                      >
                        Search
                      </Button>
                    </Tooltip>{' '}
                  </div>
                  <div className='inner p-1'>
                    <Tooltip placement='topLeft' title={'Reset'}>
                      <Button
                        type='primary'
                        size={'large'}
                        onClick={() => {
                          getBuyerWatchlist()
                        }}
                      >
                        Reset
                      </Button>
                    </Tooltip>{' '}
                  </div>
                </div>
              </div>
            </div>
            <ul className='nav nav-custom nav-tabs nav-line-tabs nav-line-tabs-2x border-0 fs-4 fw-bold mb-8 main-table'>
              <li className='nav-item'>
                <a
                  className='nav-link text-active-primary pb-4 active'
                  data-bs-toggle='tab'
                  href='#Offers'
                  onClick={() => {
                    setActivemenu('Offers')
                    setCurrentPage(1)

                    setsorter({
                      columnKey: 'AsOfDate',
                      order: 'descend',
                    })
                  }}
                >
                  OFFERS
                </a>
              </li>
              <li className='nav-item'>
                <a
                  className='nav-link text-active-primary pb-4 '
                  data-bs-toggle='tab'
                  href='#BuyerWatchlist'
                  onClick={() => {
                    setActivemenu('BuyerWatchlist')
                    setCurrentPage(1)

                    setsorter({
                      columnKey: 'AsOfDate',
                      order: 'descend',
                    })
                  }}
                >
                  BUYER WATCHLIST
                </a>
              </li>

              <li className='nav-item'>
                <a
                  className='nav-link text-active-primary pb-4'
                  data-kt-countup-tabs='true'
                  data-bs-toggle='tab'
                  href='#Buyerintel'
                  onClick={() => {
                    setActivemenu('Buyerintel')
                    setCurrentPage(1)
                    setsorter({
                      columnKey: 'AsOfDate',
                      order: 'descend',
                    })
                  }}
                >
                  BUYER INTEL
                </a>
              </li>
            </ul>
            <div className='tab-content' id='myTabContent'>
              <div className='tab-pane fade show active ' id={activeMenu} role='tabpanel'></div>
            </div>

            <Table
              columns={columns}
              dataSource={data}
              pagination={false}
              loading={loading}
              className='p-4'
              size='large'
              rowKey={(record) => record.pid}
              onChange={(val: any, filter: any, sorter: any, extra: any) => {
                setsorter({
                  ...sorter,
                })
              }}
            />
            <div className='row text-end m-4'>
              <Pagination defaultCurrent={currentPage} total={rowCnt} defaultPageSize={20}  onChange={(e)=>setCurrentPage(e)}/>
            </div>
            <Modal open={isModalOpen} onCancel={handleCancel} footer={false} destroyOnClose>
              <Form
                name='time_related_controls'
                // {...formItemLayout}
                onFinish={onFinish}
                style={{maxWidth: 600}}
              >
                <Form.Item name='daterange' label='From Date - To Date'>
                  <RangePicker />
                </Form.Item>
                <Form.Item name='productcode' label='Product Code'>
                  <Input placeholder='Search Product' />
                </Form.Item>
                <Form.Item>
                  <Button type='primary' htmlType='submit'>
                    Submit
                  </Button>
                </Form.Item>
              </Form>
            </Modal>
          </div>
        </div>
      </div>
    </>
  )
}
export default BuyersWatchlist
