import { mainConfig } from "../../../config/mainConfig"
const token=JSON.parse(localStorage.getItem('portalUserDataToken') || '')

export const Apifordataget =(api:any)=>{
    return new Promise<void>((resolve, reject) => {
        try {
            fetch(`${mainConfig.MAIN_ENDPOINT}${api}`, {
              method: 'get',
              headers: {
                Authorization: `Bearer ${token}`,
                Accept: 'application/json',
                'Content-Type': 'application/json',
              },
            })
              .then((data: any) => {
                data.json().then(function (resp: any) {
                    resolve(resp)
                })
              })
              .catch(function (error) {
                console.log('Request failed', error)
              })
          } catch (err) {
            console.log('ERR>>>', err)
          }
    })
}
export const ApifordataPost =(api:any,data:any)=>{

  // return 
  return new Promise<void>((resolve, reject) => {
      try {
          fetch(`${mainConfig.MAIN_ENDPOINT}${api}`, {
            method: 'Post',
            headers: {
              Authorization: `Bearer ${token}`,
              Accept: 'application/json',
              'Content-Type': 'application/json',
            },
            body: JSON.stringify(data)
          })
            .then((data: any) => {
              data.json().then(function (resp: any) {
                  resolve(resp)
              })
            })
            .catch(function (error) {
              console.log('Request failed', error)
            })
        } catch (err) {
          console.log('ERR>>>', err)
        }
  })
}